.page {
  display: flex;
  min-height: 100vh;
}

/* =====================
   ЛІВА ЧАСТИНА
   ===================== */
.formSide {
  flex: 1;
  padding: 72px 0 64px 64px;
  box-sizing: border-box;
}

/* container як на макеті */
.container {
  max-width: 592px;
  margin: 0 auto;
  padding-left: 15px;
}

/* wrapper як на макеті */
.wrapper {
  width: 480px;
  margin: 0 auto;
  padding: 57px 56px;
}

/* title */
.title {
  font-family: "Comfortaa", sans-serif;
  text-align: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  letter-spacing: 0.01em;
  margin-bottom: 32px;
}

.imageSide {
  position: sticky;
  top: 0;
  right: 0;
  width: 50vw;
  height: 100vh;
  overflow: hidden;
}

.imageWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
}

.image {
  height: 100%;
  width: auto;
  object-fit: cover;
  object-position: right center;
}

/* =====================
   BREAKPOINTS
   ===================== */
@media (min-width: 1920px) {
  .imageSide {
    width: 45vw;
  }
}

@media (max-width: 1200px) {
  .imageSide {
    width: 55vw;
  }
}

/* MOBILE */
@media (max-width: 767px) {
  .imageSide {
    display: none;
  }
}